import { useState, useEffect, useRef } from 'react'
import './page.css';

//graphviz
import * as d3 from 'd3'
import * as d3Graphviz from 'd3-graphviz';
//https://www.npmjs.com/package/svg-pan-zoom
import svgPanZoom from "svg-pan-zoom";

//split
import { Allotment } from "allotment";
import "allotment/dist/style.css";


//tabs
//import { Tab, Tabs, TabList, TabPanel } from 'react-tabs';
//import 'react-tabs/style/react-tabs.css';

//tabs
import LeftPane from './LeftPane';
import RightGraph from './RightGraph';
import RightTab from './RightTab';


function Graph() {
    const panZoomRef = useRef();
    const [doc, setDoc] = useState([]);
    const [dot, setDot] = useState();

    const searchField = async (field) => {
      console.log('clicked search: [' +field+']');
        showDoc('clicked search: [' +field+']');
    }

    const fetchApp = async () => {
        showDoc('table loading');
      const res = await fetch('http://tt15:9090/api/graph/sample/OVERVIEW/app')
      showDoc('app loaded');
      return await res.json()
    }
    const fetchTable = async (table) => {
      const appName = "sample";
      const encodedField = table;
      const level = "table";
      const res = await fetch('http://tt15:9090/api/graph/'+appName+'/'+encodedField+"/"+level)
      showDoc('table loaded');
      return await res.json()
    }
    const showApp = async () => {
      const data = await fetchApp()
      renderGraph(data.graph);
    }

    const showTable = async (table) => {
        showDoc('clicked table: [' +table+']')
      console.log('clicked table: [' +table+']');
      const data = await fetchTable(table)
      renderGraph(data.graph);

    }

    const showRelation = async (relation) => {
        showDoc('clicked relation: [' +relation+']')
      console.log('clicked relation: [' +relation+']');
    }
    const showDoc = (docSrc) => {
      console.log('show doc: [' +doc+']');
//      if(doc.length==5) {
//        doc.shift();
//      }
      //doc.push(docSrc+"\n")
      setDoc( // Replace the state
        [ // with a new array
          ...doc, // that contains all the old items
          docSrc // and one new item at the end
        ]
      );
    }
    const showDot =  (dot) => {
      console.log('show dot: [' +dot+']');
      setDot(dot);
    }
    const renderGraph = (dotSrc) => {
        showDoc('graph rendering');
        showDot(dotSrc);
        d3.select("#graphCanvas").selectAll('*').remove();
        d3.select("#graphCanvas")
        .graphviz()
        .zoom(false)//disable d3 graphviz zoom, to make svgPanZoom work
        .dot(dotSrc)
        .render()
        .on("end", function(){
            console.log("Graph rendered successfully")
            panZoomRef.current = svgPanZoom("#graphCanvas svg", {controlIconsEnabled: true})
            const nodes = document.querySelectorAll("#graphCanvas svg .graph_node_table")
            nodes.forEach(node => {
                node.addEventListener('click', function () {
                    const s = this.innerHTML;
                    const f = s.substring(s.indexOf("<title>")+7,s.indexOf("</title>"));
                    showTable(f);
                });
            });


            const labels = document.querySelectorAll("#graphCanvas svg .graph_label")
            labels.forEach(label => {
                label.addEventListener('click', function () {
                    const s = this.innerHTML;
                    const f = s.substring(s.indexOf("<title>")+7,s.indexOf("</title>"));
                    showRelation(f);
                });
            });

            showDoc('graph rendered');
        });
    }

    const handleResize = () => {
        //console.log('resized to: ', window.innerWidth, 'x', window.innerHeight +": "+panZoomRef)
        if(panZoomRef.current != null) {
            const divElement = document.querySelector("#graphCanvas")
            if (divElement) {
                panZoomRef.current.resize();
//                panZoomRef.current.fit();
                //panZoomRef.current.center();
            }
        }
    }


    useEffect(() => {
        console.log("loaded Home")
        showApp()
        console.log("add resize")
        window.addEventListener('resize', handleResize)
    }, [])//render only once


    function RightGraph2() {
//        return (
//            <Tabs style={{height: '100%', display: 'flex', flexDirection: 'column'}}>
//               <TabList>
//                 <Tab>Graph</Tab>
//                 <Tab>DOT</Tab>
//               </TabList>
//               <TabPanel style={{flex: 1}}>
//                   <div style={{ border: '10px solid red',
//                                height:'100%'//parent display:block
//                           }}>
//                        <div id="graphCanvas"></div>
//                   </div>
//               </TabPanel>
//             </Tabs>
//        );
    }


    return (
            <Allotment >
              <Allotment.Pane minSize={2} preferredSize={250}>
                <LeftPane search={searchField}/>
              </Allotment.Pane>
              <Allotment.Pane minSize={200} >
                <Allotment vertical={true} onChange={handleResize}>
                  <Allotment.Pane minSize={200}>
                    <RightGraph />
                  </Allotment.Pane>
                  <Allotment.Pane minSize={2} preferredSize={200}>
                    <RightTab doc={doc} dot={dot}/>
                  </Allotment.Pane>
                </Allotment>
              </Allotment.Pane>
            </Allotment>
    );
}

export default Graph;

