import Box from '@mui/material/Box';
import TextField from '@mui/material/TextField';
import List from '@mui/material/List';
import Stack from '@mui/material/Stack';
import Button from '@mui/material/Button';
import IconButton from '@mui/material/IconButton';
import SearchIcon from '@mui/icons-material/Search';
//trees
import { SimpleTreeView } from '@mui/x-tree-view/SimpleTreeView';
import { TreeItem } from '@mui/x-tree-view/TreeItem';
function LeftPane({search}) {
//<Button variant="contained" onClick={search} >Search</Button>

    function SearchBox({search}) {
      return (
        <Stack spacing={1} direction="row">
            <space></space><space></space>
            <TextField fullWidth id="standard-basic" variant="standard" margin="dense"/>

            <IconButton color="primary" aria-label="add to shopping cart" onClick={search} >
              <SearchIcon />
            </IconButton>
        </Stack>
      );
    }
    function LeftTree() {
      return (
          <Box sx={{ minHeight: 352, minWidth: 250 }}>
            <SimpleTreeView>
              <TreeItem itemId="grid" label="Data Grid">
                <TreeItem itemId="grid-community" label="@mui/x-data-grid" />
                <TreeItem itemId="grid-pro" label="@mui/x-data-grid-pro" />
                <TreeItem itemId="grid-premium" label="@mui/x-data-grid-premium" />
              </TreeItem>
              <TreeItem itemId="pickers" label="Date and Time Pickers">
                <TreeItem itemId="pickers-community" label="@mui/x-date-pickers" />
                <TreeItem itemId="pickers-pro" label="@mui/x-date-pickers-pro" />
              </TreeItem>
              <TreeItem itemId="charts" label="Charts">
                <TreeItem itemId="charts-community" label="@mui/x-charts" />
              </TreeItem>
              <TreeItem itemId="tree-view" label="Tree View">
                <TreeItem itemId="tree-view-community" label="@mui/x-tree-view" />
              </TreeItem>

                <TreeItem itemId="grid1" label="Data Grid">
                  <TreeItem itemId="grid-community1" label="@mui/x-data-grid" />
                  <TreeItem itemId="grid-pro1" label="@mui/x-data-grid-pro" />
                  <TreeItem itemId="grid-premium1" label="@mui/x-data-grid-premium" />
                </TreeItem>
                <TreeItem itemId="pickers1" label="Date and Time Pickers">
                  <TreeItem itemId="pickers-community1" label="@mui/x-date-pickers" />
                  <TreeItem itemId="pickers-pro1" label="@mui/x-date-pickers-pro" />
                </TreeItem>
                <TreeItem itemId="charts1" label="Charts">
                  <TreeItem itemId="charts-community1" label="@mui/x-charts" />
                </TreeItem>
                <TreeItem itemId="tree-view1" label="Tree View">
                  <TreeItem itemId="tree-view-community1" label="@mui/x-tree-view" />
                </TreeItem>
            </SimpleTreeView>
          </Box>
      );
    }

    function ScrollPane({ children }) {

      return (
        <div style={{

                height: '100%',
                border: '3px solid blue',
                overflowX: 'hidden',
                overflowY: 'auto' //show vertical scroll bar
            }}
            >
          {children}
        </div>
      );
    }
    return (
        <div style={{
                    height:'100%',//parent display:block
                    border: '1px solid red',

                    display: 'flex',//control children
                    flexDirection: 'column'//control children
                }}>
            <SearchBox search={search}/>

            <ScrollPane><LeftTree /></ScrollPane>
        </div>
    );
}
//            <div style={{
//                    flex: 1,//fill the rest space
//                    border: '0px solid blue',
//                    overflowX: 'hidden',
//                    overflowY: 'auto' //show vertical scroll bar
//                }}
//                >
//
//            </div>
export default LeftPane;